a {
  text-decoration: none;
}

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Josefin Sans', sans-serif !important;
}

#root {
  max-width: 1200px;
  margin: 0 auto;
}

.Welcome {
  background-image: linear-gradient(120deg, #7a83b8 0%, #a6c1ee 100%);
  height: 100%;
  background-size: cover;
}

.middle {
  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}

.bottomleft {
  position: absolute;
  bottom: 0;
  left: 16px;
}

.btn {
  background-color: transparent;
  border-radius: 2em;
  border: 0.1em solid #1ecd97;
  color: #1ecd97;
  cursor: pointer;
  font-size: 2vmin;
  padding: 0.7em 1.5em;
  transition: all 0.25s ease;
}

.btn:hover {
  background: #1ecd97;
  color: #333;
}

.btn--login {
  margin: 0 auto;
}

.github {
  text-decoration: none !important;
  transition: all 0.25s ease;
}

.github:hover {
  color: #333;
  opacity: 0.2;
}
.animate-top {
  position: relative;
  -webkit-animation: animatetop 2s;
          animation: animatetop 2s;
}

@-webkit-keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }

  to {
    top: 0;
    opacity: 1;
  }
}

@keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }

  to {
    top: 0;
    opacity: 1;
  }
}

